.basic-slider {
  position: relative;
  .item {
    width: 100%;
    display: flex;
    flex-direction: column;
    .row{
      @include media((
              height: (all: 100%, sm: auto)
      ));
    }
    .img-container {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: auto;
        transform: translate3d(0px, 0px, 0px);
      }
      &:hover {
        img {
          opacity: 0.8 !important;
        }
      }
    }
    .text-container{
      @include media((
              margin-top: (sm: 15px),
              text-align: (sm: center)
      ));
      .name {
        @include media((
                font-size: (all: 28px, lg: 22px)
        ));
      }
      .text {
        @include media((
                margin-top: (all: 20px, lg: 15px),
                text-align: (sm: left)
        ));
        //@extend %text-elements;
      }
      .button {
        @include media((
                margin-top: (all: 20px, lg: 15px)
        ));
      }
    }

    &.item-bg {
      padding: 15px;
    }
  }
  .owl-carousel {
    margin-bottom: 30px;
    .owl-stage{
      display: flex;
      .owl-item {
        display: flex;
        flex: 1 0 auto;
      }
    }
    /**/
    @include owl-slider2-nav-vector(
      // 
            $owl-slider2-nav-width: null, //       - ,              (  null)
            $owl-slider2-nav-height: null,
            $owl-slider2-nav-left-right: 24px, //  /
            $owl-slider2-nav-margin-top: null, //    (     ) (  null)
            $owl-slider2-nav-opacity: 0.6, // 
            $owl-slider2-nav-opacity-hover: 1, //   
            $owl-slider2-nav-padding: 'true', //      ('true'  null)

              //  - fontawesome
            $owl-slider2-nav-is-awesome: 'true', //     fontawesome ('true'  null)
            $owl-slider2-nav-prev-code: "\f053", //   
            $owl-slider2-nav-next-code: "\f054", //   
            $owl-slider2-nav-font-size: 24px, //   
            $owl-slider2-nav-color: #fff, //  
            $owl-slider2-nav-color-hover: #fff, //    
            $owl-slider2-nav-text-shadow: 0px 3px 1px rgba(0, 0, 0, 0.6), //    (  none)
            $owl-slider2-nav-bg-color: null, //   (  null)
            $owl-slider2-nav-bg-color-hover: null, //     (  null)
            $owl-slider2-nav-icon-bg-left-right: 0, //      ,     (, -2px)

              //  - 
            $owl-slider2-nav-is-img: null, //       ('true'  null)
            $owl-slider2-nav-prev-bg: none, //   - : url('../img/arr-left.png')  none
            $owl-slider2-nav-next-bg: none, //  - : url('../img/arr-left.png')  none
            $owl-slider2-nav-prev-bg-hover: none, //   -  - : url('../img/arr-left.png')  none
            $owl-slider2-nav-next-bg-hover: none //   -  - : url('../img/arr-left.png')  none
    );

    @include owl-slider2-dots();
  }
  &.one-slide{ // one-slide    ,    
    .owl-carousel {
      display: block;
      img{
        display: block;
        width: 100%;
      }

    }
  }
}