/* carousel owl slider */
.carousel-slider {
  position: relative;
  .item {
    display: flex;
    flex-direction: column;
    width: 100%;
    border: 1px solid #ddd;
    text-decoration: none;
    text-align: center;
    background: #fff;
    transition: border 0.1s ease-in-out;

    @include media((
            padding: (all: 20px 15px, sm: 15px, xs: 10px)
    ));
    .img-container {
      display: inline-block;
      text-decoration: none;
      img{
        width: auto;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .text-container {
      display: flex;
      flex: 1 0 auto;
      flex-direction: column;
      .title {
        font-size: 16px;
        font-weight: bold;

        text-overflow: ellipsis;
        overflow: hidden;
        @include media((
                margin-top: (all: 10px, xs: 7px)
        ));

        //@extend %text-elements;
      }
      a.title{
        display: inline-block;
        text-decoration: none;
      }
      .text {
        font-size: 14px;
        text-overflow: ellipsis;
        overflow: hidden;
        @include media((
                margin-top: (all: 10px, xs: 7px)
        ));

        flex-grow: 1;
        //@extend %text-elements;
      }
      .button {
        @include media((
                margin-top: (all: 10px, xs: 7px)
        ));
        align-self: center;
      }
    }
    &:hover {
      border: 1px solid #a2a2a2;
      transition: border 0.1s ease-in-out;
    }
  }
  .owl-carousel {
    margin-bottom: 30px;
    .owl-stage {
      display: flex;
      margin-left: auto;
      margin-right: auto;
      .owl-item {
        display: flex;
        flex: 1 0 auto;
      }
    }

    @include owl-slider2-nav-vector(
      // 
            $owl-slider2-nav-width: null, //       - ,              (  null)
            $owl-slider2-nav-height: null,
            $owl-slider2-nav-left-right: 24px, //  /
            $owl-slider2-nav-margin-top: null, //    (     ) (  null)
            $owl-slider2-nav-opacity: 0.6, // 
            $owl-slider2-nav-opacity-hover: 1, //   
            $owl-slider2-nav-padding: 'true', //      ('true'  null)

              //  - fontawesome
            $owl-slider2-nav-is-awesome: 'true', //     fontawesome ('true'  null)
            $owl-slider2-nav-prev-code: "\f053", //   
            $owl-slider2-nav-next-code: "\f054", //   
            $owl-slider2-nav-font-size: 24px, //   
            $owl-slider2-nav-color: #fff, //  
            $owl-slider2-nav-color-hover: #fff, //    
            $owl-slider2-nav-text-shadow: 0px 3px 1px rgba(0, 0, 0, 0.6), //    (  none)
            $owl-slider2-nav-bg-color: null, //   (  null)
            $owl-slider2-nav-bg-color-hover: null, //     (  null)
            $owl-slider2-nav-icon-bg-left-right: 0, //      ,     (, -2px)

              //  - 
            $owl-slider2-nav-is-img: null, //       ('true'  null)
            $owl-slider2-nav-prev-bg: none, //   - : url('../img/arr-left.png')  none
            $owl-slider2-nav-next-bg: none, //  - : url('../img/arr-left.png')  none
            $owl-slider2-nav-prev-bg-hover: none, //   -  - : url('../img/arr-left.png')  none
            $owl-slider2-nav-next-bg-hover: none //   -  - : url('../img/arr-left.png')  none
    );

    @include owl-slider2-dots();
  }
}
// firefox hack for border right item
_:-moz-tree-row(hover), .page__main-column .carousel-slider .owl-carousel {
  width: 100.3%;
}

/* carousel owl slider custom */
.carousel-slider-custom {
  position: relative;
  .item{
    width: 100%;
  }
  .owl-carousel {
    margin-bottom: 30px;
    width: calc(100% + 3px); //border right item fix
    .owl-stage {
      display: flex;
      margin-left: auto;
      margin-right: auto;
      .owl-item {
        display: flex;
        flex: 1 0 auto;
        img{
          width: initial;
        }
      }
