  //     item  
  &.item-with-shadow{
    .owl-stage-outer{
      width: calc(100% + 60px);
      padding: 0 30px;
      margin-left: -30px;
    }
    .owl-carousel{
      margin-top: -15px;
    }
    .owl-item{
      padding-top: 15px;
      padding-bottom: 40px;
    }
    .button-container{
      @include media((
              margin-top: (all: -30px, md:-20px)
      ));
    }
    .owl-dots{
      margin-top: -25px!important;
    }
  }
  // /    item  

}